import { Avatar, Box, Button, Grid, Link, TextField, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import React from 'react'
import * as Yup from 'yup';
import { useNavigate } from 'react-router';
import { theme } from '../../configtheme';
import { NormalButton } from '../buttons/NormalButton';
import { InputText } from '../inputs/InputText';
import { Form, Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import { authCreateRegister } from '../../redux/actions/auth.actions';
import { Loading } from '../Loadings/Loading';
import ErrorAlert from '../Alerts/ErrorAlert';


const initialValues = {
    name: '',
    lastName: '',
    email: '',
    password: '',
    password2: '',
}

const validation = Yup.object({
    name: Yup.string().max(50, 'Debe de tener 50 caracteres o menos').required('Requerido'),
    lastName: Yup.string().max(50, 'Debe de tener 50 caracteres o menos').required('Requerido'),
    email: Yup.string().email('Email Invalido').required('Requerido'),
    password: Yup.string().min(3, 'Debe tener al menos 3 caracteres').required('Requerido'),
    password2: Yup.string().oneOf([Yup.ref('password')], 'Password no coinciden')
        .min(3, 'Debe tener al menos 3 caracteres').required('Requerido'),


})

export const Register = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const handleSubmit = (data) => {
        dispatch(authCreateRegister(data));
    };

    const GotoLogin = () => {
        navigate("/login");
    }
    return (
        <Box
            sx={{
                marginTop: 6,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
        >
            <Avatar sx={{ bgcolor: theme.palette.valen.secondary }}>
                <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
                Registro
            </Typography>
            <Formik
                initialValues={initialValues}
                onSubmit={(values) => {
                    // console.log(values);
                    handleSubmit(values);
                }}
                validationSchema={validation}
            >

                {({ handleReset, isValid, errors }) => (
                    <Form component="form" noValidate >
                        <Grid container spacing={1}>
                            <Grid item xs={12} sm={6}>
                                <InputText
                                    autoComplete="given-name"
                                    name="name"
                                    required
                                    fullWidth
                                    id="name"
                                    label="Nombre"
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <InputText
                                    required
                                    fullWidth
                                    id="lastName"
                                    label="Apellido"
                                    name="lastName"
                                    autoComplete="family-name"

                                />
                            </Grid>
                            <Grid item xs={12}>
                                <InputText
                                    required
                                    fullWidth
                                    id="email"
                                    type="email"
                                    label="Email"
                                    name="email"
                                    autoComplete="email"

                                />
                            </Grid>
                            <Grid item xs={12}>
                                <InputText
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="new-password"

                                />
                            </Grid>
                            <Grid item xs={12}>
                                <InputText
                                    required
                                    fullWidth
                                    name="password2"
                                    label="Confirmar Password"
                                    type="password"
                                    id="password2"
                                    autoComplete="new-password"

                                />
                            </Grid>

                        </Grid>
                        <NormalButton disabled={!isValid} text="Registrarse" type="submit" />
                        <Grid container justifyContent="flex-end">
                            <Grid item>
                                <Link onClick={GotoLogin} variant="body2">
                                    Ya tiene cuenta? Inicie sesion
                                </Link>
                            </Grid>
                        </Grid>
                    </Form>

                )}


            </Formik>
            <Loading />
            <ErrorAlert/>
        </Box>
    )
}